.logo {
  width: 200px;
  height: 31px;
  background: rgb(255, 255, 255);
  margin: 16px 28px 16px 0;
  float: left;
}

[type="file"] {
  height: 0;
  overflow: hidden;
  width: 0;
}

[type="file"] + label {
  background: #1890ff;
  border: none;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-family: 'Rubik', sans-serif;
  font-size: inherit;
  font-weight: 500;
  margin-bottom: 1rem;
  outline: none;
  padding: 1rem 50px;
  position: relative;
  transition: all 0.3s;
  vertical-align: middle;
}

[type="file"] + label:hover {
  background: #40a9ff;
}
[type="file"]:focus + label,
[type="file"] + label:hover {
    background: #40a9ff;
}

[type="file"]:focus + label {
  outline: 1px dotted #000;
}

.extra > img
{
  width:64px !important;
  height:64px !important;
  
}